@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Add these custom gradients */
.gradient-primary {
  @apply bg-gradient-to-r from-blue-600 via-indigo-500 to-purple-500;
}

.gradient-secondary {
  @apply bg-gradient-to-r from-pink-500 via-rose-500 to-red-500;
}

.gradient-success {
  @apply bg-gradient-to-r from-green-400 to-emerald-500;
}

.gradient-info {
  @apply bg-gradient-to-r from-blue-400 to-cyan-500;
}

/* Add hover effects */
.hover-lift {
  @apply transition-transform duration-200 hover:-translate-y-1;
}

/* Add card glow effects */
.card-glow {
  @apply hover:shadow-lg hover:shadow-indigo-500/20 transition-shadow duration-200;
}
